import axios from "axios";
import { toast } from "react-toastify";

const BASE_URL = 'https://9817-2401-4900-8811-b005-a110-bbb4-3c86-2b56.ngrok-free.app/';

export const CONTENT_TYPES = {
    APPLICATION_JSON: "application/json",
    MULTIPART_FORM: "multipart/form-data",
    URL_ENCODED: "application/x-www-form-urlencoded",
};

const getFormData = (body, type) => {
    let formData =
        type === CONTENT_TYPES.MULTIPART_FORM
            ? new FormData()
            : new URLSearchParams();
    Object.entries(body).forEach(([key, value]) => {
        if (
            Array.isArray(value) &&
            (key === "file_upload" || key === "attachment")
        ) {
            value.forEach((file, index) => {
                formData.append(`${key}[${index}]`, file);
            });
        } else if (Array.isArray(value)) {
            value.forEach((item, index) => {
                if (typeof item === "object") {
                    Object.entries(item).forEach(([objKey, objValue]) => {
                        formData.append(
                            `${key}[${index}][${objKey}]`,
                            objValue
                        );
                    });
                } else {
                    formData.append(`${key}[${index}]`, item);
                }
            });
        } else {
            formData.append(key, value);
        }
    });
    if (type === CONTENT_TYPES.URL_ENCODED) {
        return formData.toString();
    }
    return formData;
};

export const extractErrorMessage = (err) => {
    let message = err.message;
    if (
        typeof err?.response?.data === "object" &&
        Object.keys(err?.response?.data)?.length
    ) {
        if (err?.response?.data?.message) {
            console.log("errr---------1", err);
            message = err?.response?.data?.message;
        } else {
            console.log("errr---------2", err);
            const errorData = Object.values(err?.response?.data)[0];
            if (errorData?.length) message = errorData;
            // if (errorData?.length) message = errorData[0];
        }
    }
    return message;
};

export default class REQUEST {
    async get(
        route,
        body = {},
        headers = {},
        onSuccess = Function.prototype,
        onError = Function.prototype,
        noToast
    ) {
        try {
            const response = await axios.get(BASE_URL + route, {
                headers: { Accept: "*/*", ...headers },
                data: body,
            });
            onSuccess(response);
            return response;
        } catch (err) {
            if (err.response.status === 401) {
                localStorage.setItem("t_token", "");
                window.location.replace("/oplogin");
            }
            let message = extractErrorMessage(err);
            !noToast && toast.error(message || "Something might went wrong!");
            const result = { error: true, message: message };
            onError && onError(result);
            return result;
        }
    }

    async post(
        route,
        body,
        headers = {},
        onSuccess = () => {},
        onError = () => {},
        noToast
    ) {
        try {
            let formData = body;
            if (
                headers["Content-Type"] === CONTENT_TYPES.MULTIPART_FORM ||
                headers["Content-Type"] === CONTENT_TYPES.URL_ENCODED
            ) {
                formData = getFormData(body, headers["Content-Type"]);
            }

            const response = await axios.post(BASE_URL + route, formData, {
                headers: { Accept: "*/*", ...headers },
            });
            onSuccess(response);
            return response;
        } catch (err) {
            let message = extractErrorMessage(err);
            !noToast && toast.error(message || "Something might went wrong!");
            const result = { error: true, message: message };
            onError(result);
            return result;
        }
    }

    async put(
        route,
        body,
        headers = {},
        onSuccess = () => {},
        onError = () => {},
        noToast
    ) {
        try {
            let formData = body;
            if (headers["Content-Type"] === CONTENT_TYPES.MULTIPART_FORM) {
                formData = getFormData(body);
            }
            const response = await axios.put(BASE_URL + route, formData, {
                headers: { Accept: "*/*", ...headers },
            });
            onSuccess(response);
            return response;
        } catch (err) {
            let message = extractErrorMessage(err);
            !noToast && toast.error(message || "Something might went wrong!");
            const result = { error: true, message: message };
            onError(result);
            return result;
        }
    }

    async delete(
        route,
        headers = {},
        onSuccess = () => {},
        onError = () => {},
        noToast
    ) {
        try {
            const response = await axios.delete(BASE_URL + route, {
                headers: { Accept: "*/*", ...headers },
            });
            onSuccess(response);
            return response;
        } catch (err) {
            let message = extractErrorMessage(err);
            !noToast && toast.error(message || "Something might went wrong!");
            const result = { error: true, message: message };
            onError(result);
            return result;
        }
    }
}
